<template>
    <layout-default>
        <div class="page-content">
            <div class="container-fluid">
                <!-- start page title -->
                <div class="row">
                    <div class="col-12">
                        <div class="page-title-box d-flex align-items-center justify-content-between">
                            <h4 class="mb-0">جدول الدخل</h4>
                        </div>
                    </div>
                </div>
                <!-- end page title -->
                <div class="row">
                    <div class="col-12">
                        <div class="card">
                            <div class="card-body">
                                <div class="row mb-4">
                                    <div class="col-md-3">
                                        <Datepicker @input="fetchData(1)" placeholder="From" input-class="form-control" v-model="filter.from" format="dd-MM-yyyy"></Datepicker>
                                    </div>
                                    <div class="col-md-3">
                                        <Datepicker @input="fetchData(1)" placeholder="To" input-class="form-control" v-model="filter.to" format="dd-MM-yyyy"></Datepicker>
                                    </div>
                                    <div class="col-md-2">
                                        <div class="form-check mt-2">
                                            <input v-model="filter.types" @change="fetchData(1)" value="inputs" type="checkbox" class="form-check-input" id="formrow-customCheck">
                                            <label class="form-check-label font-size-16" for="formrow-customCheck" style="font-weight:bold;">إيرادات</label>
                                        </div>
                                    </div>
                                    <div class="col-md-2">
                                        <div class="form-check mt-2">
                                            <input v-model="filter.types" @change="fetchData(1)" value="payments" type="checkbox" class="form-check-input" id="formrow-customCheck2">
                                            <label class="form-check-label font-size-16" for="formrow-customCheck2" style="font-weight:bold;">مصروفات</label>
                                        </div>
                                    </div>
                                    <div class="col-md-2">
                                        <a @click="clear" class="btn btn-primary" href="javascript:;">Clear</a>
                                    </div>
                                </div>
                                <div class="row mb-4">
                                </div>
                            </div>
                        </div>
                        <div class="col-md-12">
                            <div class="card me-1" v-if="rows&&rows.length">
                                <div class="card-body">
                                    <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                                    <thead>
                                        <tr>
                                            <th>ملاحظات</th>
                                            <th>البيان</th>
                                            <th>النوع</th>
                                            <th>المبلغ</th>
                                            <th>التاريخ</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(row,index) in rows" >
                                            <td>{{row.note1}} </td>
                                            <td>{{row.note}} </td>
                                            <td>{{row.type}} </td>
                                            <td v-if="row.status==1" class="text-success" style="font-weight:bold;">
                                                 <i class="fas fa-arrow-down"></i> {{moneyFormat(row.money,0)}}
                                            </td>
                                            <td v-else class="text-danger" style="font-weight:bold;">
                                                 <i class="fas fa-arrow-up"></i> {{moneyFormat(row.money,0)}}
                                            </td>
                                            <td>{{row.created_at| moment("DD-MM-YYYY")}}</td>
                                        </tr>
                                    </tbody>
                                    </table>
                                </div>
                                <div class="col-12 ms-2" v-if="pageSize>1">
                                    <nav aria-label="Page navigation example">
                                        <ul class="pagination">
                                            <li class="page-item" :class="{ 'disabled': currentPage==1 }"><a class="page-link" @click="gotoPage(currentPage-1)" href="javascript:;">Previous</a></li>
                                            <li v-for="index in pageSize" :key="index" class="page-item" v-bind:class="{ 'active': index==currentPage }">
                                                <a class="page-link" @click="gotoPage(index)" href="javascript:;">{{ index  }}</a>
                                            </li>
                                            <li class="page-item" :class="{ 'disabled': currentPage==pageSize}"><a class="page-link"  @click="gotoPage(currentPage+1)" href="javascript:;">Next</a></li>
                                        </ul>
                                    </nav>
                                </div>
                            </div>
                            <div class="card ms-1" v-else>
                                <div class="card-body">
                                    <h5 class="font-size-20 mb-4 mt-5 text-center" style="font-weight:600;">كشف حساب المورد</h5>
                                    <p class="text-center">لا يوجد نتائج</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!-- end col -->
                </div>
                <!-- end row -->
            </div>
            <!-- container-fluid -->
        </div>
    </layout-default>
</template>
<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import Datepicker from 'vuejs-datepicker';
import FormModal from '../../components/modals/input_type/add';
import axios from 'axios';
export default {
    components: { LayoutDefault, PaginationNav, FormModal,Datepicker },
    data() {
        return { 
            currentPage: 1,
            pageSize: 5,
            data: null,
            rows: null,
            loader: false,
            filter:{
                types:['inputs','payments'],
                from:null,
                to:null,
            }
        }
    },
    created() {
        this.fetchData();
    },
    methods: {
        clear() {
            this.filter.from=null
            this.filter.to=null
            this.fetchData(1)
        },
        gotoPage(pageNumber= 1) {
            this.currentPage=pageNumber;
            this.rows=this.data.slice((pageNumber - 1) * 10, pageNumber * 10);
            console.log(pageNumber,this.rows);
        },
        fetchData(num = 1) {
            this.currentPage=num
            this.rows = null;
            this.loader = true;
            let data = {params: {filter:this.filter}};
            axios.get('/api/reports/money', data).then(({ data }) => {
                this.data = data.success;
                let len=data.success.length;
                this.pageSize=(len%10)>0?parseInt(len/10)+1:len/10;
                this.gotoPage()
                this.loader = false;
            });
        },
    },
}
</script>